.header{
    width: 100%;
    background-color: #041734;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 10;
}

.header__container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1500px;
    height: 90px;
}

ul.nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: fit-content;
    height: 100%;

    li.nav__item{
        width: auto;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        transition: background .15s ease-in;
        position: relative;

        a.nav__item__link{
            color: white;
            font-family: 'Roboto', sans-serif;
            font-weight: 400;
            padding: 0 20px;
            font-size: 14px;

            .arrow{
                margin-left: 5px;

                .svg{
                    width: 8px;
                    height: 13px;

                    path{
                        fill: white;
                    }
                }

            }
        }

        &:hover{
            background: #293d4e;
            transition: background .3s;

            a.nav__item__link{
                color: #c8d0da;

                .arrow{
                    .svg{
                        path{
                            fill: #c8d0da;
                        }
                    }
                }
            }
        }
    }

    li.nav__item.sub__nav__item{
        &:hover{
            .sub__nav{
                visibility: visible;
                opacity: 1;
                transition: opacity .3s;
            }
        }
    }
}

ul.sub__nav{
    opacity: 0;
    transition: opacity .15s ease-in;
    visibility: hidden;
    position: absolute;
    top: 90px;
    left: 0;
    width: auto;
    max-width: 200px;
    min-width: 90px;

    li.nav__item{
        background: #293d4e;
        padding: 10px 0;
        justify-content: flex-start;
        width: 100%;
        align-items: none;

        a.nav__item__link{
            width: max-content;
        }

        &:hover{

            a.nav__item__link{
                color: white;
            }
            
        }
    }
}

.header__other{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 360px;
    height: 100%;
}

.select__language{
    cursor: pointer;
    position: relative;
    transition: background .15s ease-in;
    height: 100%;

    &:hover{
        background: #293d4e;
        transition: background .3s;

        .other__language{
            visibility: visible;
            opacity: 1;
            transition: opacity .3s;
        }
        
        .lang__item{
            p{
                color: #c8d0da;
            }

            .arrow{
    
                .svg{
    
                    path{
                        fill: #c8d0da;
                    }
                }
    
            }
        }
    }

    .defalt__languge{
        height: 100%;
        display: flex;
        align-self: center;
    }

    .lang__item{
        display: flex;
        align-items: center;
        font-family: 'Roboto', sans-serif;
        font-weight: 400;
        color: white;
        padding: 0 20px;

        .arrow{
            margin-left: 15px;
            margin-top: 2px;

            .svg{
                width: 10px;
                height: 16.25px;

                path{
                    fill: white;
                }
            }

        }
        
        p{
            margin-left: 15px;
        }

        .lang__icon{
            width: 27px;
            height: 27px;
            border-radius: 50%;

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 50%;
            }
        }
    }

    .other__language{
        opacity: 0;
        transition: opacity .15s ease-in;
        visibility: hidden;
        position: absolute;
        left: 0;
        top: 90px;
        width: 100%;

        .lang__item{
            background: #293d4e;
            padding: 10px 15px;
            width: 100%;

            &:hover{
                p{
                    color: white;
                }
            }
        }
    }
}

.search__btn{
    cursor: pointer;
    height: 100%;
    transition: background .15s ease-in;
    padding: 0 20px;
    display: flex;
    justify-content: center;
    align-items: center;

    &:hover{
        background: #293d4e;
        transition: background .3s;

        svg{
            fill: #c8d0da;
        }
    }

    svg{
        width: 20px;
        height: 20px;
        fill: white;
    }
}