.about{
    padding: 50px;
}


.about__header{
    position: relative;

    p.title{
        color: #020823;
        font-family: 'Roboto', sans-serif;
        font-weight: 500;
        font-size: 20px;
    }

    &::after{
        position: absolute;
        content: '';
        left: 0;
        bottom: -25px;
        background: #0052ff;
        width: 40px;
        height: 5px;
        clip-path: polygon(0 0, 100% 0, 90% 100%, 0% 100%);
    }
}

.about__content{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-top: 50px;
}

.about__slider__swiper{
    position: relative;
    display: flex;
    flex-direction: column;

    .about{
        width: 700px;
        overflow: hidden;
        padding: 0;

        .swiper-wrapper{

            .swiper-slide{
                width: 700px!important;
                display: flex;
                flex-direction: column;
            }
        }
    }
}

.swiper-pagination{
    width: 115px;
    height: 25px;
    display: flex;
    align-items: center;
    margin-top: 50px;
    align-self: center;

    .swiper-pagination-bullet{
        width: 18px;
        height: 18px;
        background-color: #123899;
        border-radius: 50%;
        margin: 0 5px;
        transition: .15s ease-in;

        &.swiper-pagination-bullet-active{
            background-color: #0052ff;
            transform: scale(1.15);
            transition: .3s linear;
        }
    }
}

.swiper-wrapper{
    min-height: 400px;
    height: 100%;

    .swiper-slide{
        height: fit-content;
        
        .slide__text{
            display: flex;
            flex-direction: column;

            p.title{
                font-family: 'Roboto', sans-serif;
                font-weight: 500;
                font-size: 20px;
                color: #020823;
            }

            p.text{
                font-family: 'Roboto', sans-serif;
                font-weight: 400;
                font-size: 17px;
                line-height: 120%;
                margin-top: 20px;
            }
        }

        .slide__icon{
            margin: 20px 0;

            .svg{
                width: 25%;
                height: 25%;

                path{
                    fill: #041734!important;
                }
            }
        }
    }
}

.slide__icon{

    .svg{

        path{
            fill: #041734!important;
        }
    }
}

.about__container{
    display: flex;
    flex-direction: column;

    .btn.--header{
        font-size: 16px;
        width: 200px;
        padding: 15px 0;
        align-self: center;
        margin-top: 50px;
    }
}

.pulse-cl{
    position: relative;
}

.pulse-cl::before {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: inherit;
    border-radius: inherit;
    transition:opacity .3s, transform .3s;
    animation: pulse-cl 1s cubic-bezier(0.24, 0, 0.38, 1) infinite;
}

@keyframes pulse-cl {
    0% {
        opacity:1;
        -webkit-transform:scale(1);
        transform:scale(1)
    }
    50% {
        opacity:0;
        -webkit-transform:scale(1.5);
        transform:scale(1.5)
    }
    100% {
        opacity:0;
        -webkit-transform:scale(1.5);
        transform:scale(1.5)
    }
}

.swiper-button-prev, .swiper-button-next{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 40px;
    height: 40px;
    border-radius: 3px;
    background-color: #0052ff;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;

    .svg{
        width: 50%;
        height: 50%;
        path{
            fill: white;
        }
    }
}

.swiper-button-prev{
    left: -70px;
}

.swiper-button-next{
    right: -50px;
}

.brands__container{
    padding: 20px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .brands__item{
        .svg{
            transition: .3s ease-in;
        }
        &:hover{
            
            .svg{
                transform: scale(1.15);
                transition: .3s linear;
            }

        }
    }
}