// reset & initial settings

*, 
*::after, 
*::before{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    border: 0;
}

a{
    text-decoration: none;
}

ul,
ol,
li{
    list-style: none;
}

img{
    vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6{
    font-weight: inherit;
    font-size: inherit;
}

html,
body{
    height: 100%;
    line-height: 1;
}

img{
    user-select: none;
}

[class*="__container"]{
    max-width: 1155px;
    width: 100%;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
}

.wrapper{
    display: flex;
    flex-direction: column;
    min-height: 100%;
    overflow: hidden;
}

main{
    flex: 1 1 auto;
}

.btn{
    border: 0;
    outline: none;
    cursor: pointer;
}

.btn.--header{
    background-color: #0052ff;
    color: white;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    font-size: 14px;
    width: 160px;
    padding: 10px 0;
    border-radius: 8px;
    transition: all .4s linear;

    &:hover{
        background-color: #1247b8;
        transform: scale(1.15);
        transition: all .5s linear;
    }
}